.advantages {
  padding: 120px 0;

  @include vp-1023 {
    padding: 78px 0;
  }

  @include vp-767 {
    padding: 50px 0 20px;
  }
}

.advantages__wrapper ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @include vp-1023 {
    row-gap: 25px;
  }

  @include vp-767 {
    row-gap: 30px;
    grid-template-columns: 1fr;
  }
}

.advantages__wrapper li {
  padding: 40px 41px 35px;
  min-height: 300px;
  border-left: 5px solid $deep-sky-blue;

  @include vp-1023 {
    min-height: 360px;
  }

  @include vp-767 {
    min-height: 340px;
    padding: 30px 25px 35px;
  }

  @include hover {
    color: $alice-blue;
    background-color: $deep-sky-blue;

    svg,
    h3 {
      color: $alice-blue;
    }
  }
}

.advantages__wrapper svg {
  margin: 0 0 15px;
  color: $deep-sky-blue;

  @include vp-1023 {
    margin: 0 0 8px;
  }


  @include vp-767 {
    margin: 0 0 15px;
    width: 30px;
    height: 30px;
  }
}

.advantages__wrapper h3 {
  margin: 0 0 15px;
  color: $deep-sky-blue;
  font-family: $ff-roboto-condensed;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;

  @include vp-1023 {
    margin: 0 0 7px;
  }

  @include vp-767 {
    margin: 0 0 10px;
    font-size: 20px;
    line-height: 140%;
  }
}

.advantages__wrapper p {
  margin: 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 135%;
  }
}
