  background-color: rgba(255, 255, 255, 0.2);
}
.footer__block a.focus-visible:focus:active {
  opacity: 0.4;
}
.footer__block a:active {
  background-color: rgba(255, 255, 255, 0.2);
  opacity: 0.4;
}

@media (max-width: 767px) {
  [data-accordion=element] .footer__title::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 15px;
    min-height: 1px;
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  [data-accordion=element]:not(.is-active) .footer__title::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%) rotate(90deg);
    width: 15px;
    min-height: 1px;
    background-color: #ffffff;
  }
}

.socials__list-wrapper ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.socials__list-wrapper a {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  user-select: none;
}
@media (hover: hover) {
  .socials__list-wrapper a:hover:not(.focus-visible) {
    opacity: 0.8;
  }
  .socials__list-wrapper a:hover:not(.focus-visible):active {
    opacity: 0.6;
  }
}
.socials__list-wrapper a.focus-visible:focus {
  opacity: 0.8;
}
.socials__list-wrapper a.focus-visible:focus:active {
  opacity: 0.6;
}
.socials__list-wrapper a:active {
  opacity: 0.6;
}
.socials__list-wrapper a span {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.socials__list-wrapper svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

